<template>
    <section class="main-content">
        <loader :is-visible="isLoading"></loader>
        <div class="padding-t_10" id="reportstabsdata">
            <div class="left-menu-tab" id="sidebarScheduleUpdate">
                <div class="col-md-12 p-0 ">
                    <div class="theme-primary partition">
                        <span class="p-name text-white">{{ navheading }}</span>
                    </div>
                </div>

                <ul class="nav nav-pills flex-column left-tab mt-2 responivecustomtab  nav-tabs">
                    <li v-for="tab in tabs" :key="tab.id" @click="activeTab = tab.id, changeTab(tab.id)"
                        :class="{ 'nav-item': true, 'active': activeTab === tab.id }">
                        <span :class="{ 'nav-link left-menu': true, 'active': activeTab === tab.id }">{{ tab.title
                            }}</span>
                    </li>
                </ul>
            </div>
            <div class="right-content">
                <div id="ReturnRequestHeading" class="theme-primary partition  p-actions-expand"><span
                        class="p-name text-white">{{ Headerstext }}</span>
                    <span class="p-actions  float-right">
                        <a class="p-action-btn text-white d-none" id="ancChangeView" data-isshow="0"><i
                                class="fa fa-pencil" aria-hidden="true"></i><br>Change View</a>
                        <a title="Page Tour" class="p-action-btn text-white" v-on:click="setTourGuideSteps(0)">
                            <em class="fa fa fa-road"></em> <br><span>Page Tour</span>
                        </a>
                    </span>
                </div>
                <div class="col-md-12 p-0" v-for="tab in tabs" :key="tab.id" v-show="activeTab === tab.id">
                    <div class="col-lg-12 border float-left py-3">
                        <div class="">
                            <dynamic-form lang="en" :buttons="button" v-if="DataLoaded"
                                :schema="FormSchema"></dynamic-form>
                        </div>
                        <div class="col-12 mb-2 px-0">
                            <div class="mr-2 d-inline-block">
                                <span class="colorboxsquare legends_profile"></span>ExpiredCheckout
                            </div>
                        </div>
                    </div>
                    <div class="tab-content mb-2">
                        <div class="listing">

                            <tg-list :showCheckBox="false" :IsShowAction="false" :listType="['List']" ModuleName="Asset"
                                SubModuleCode="Asset" IdentityColumn="ASSET_CATALOGUE_ID" :PageSize="PageSize"
                                HeaderText="Asset Management" :widgets="widgets" :ListData="CalibrationData"
                                :HeaderData="Header" :callbackfunction="FetchData" :LegendArray="LegendArray"
                                :SearchFields="leftSearchFields" :SortExp="SortExp" :SortBy="SortBy"
                                :NorecordfoundText="$t('NorecordfoundText')" :TotalRecords="TotalRecord"
                                :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchData"
                                v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
                                v-on:SortdataButtonClick="sortdata">

                                <template v-slot:slotdata="{ data }">

                                    <template v-if="data.column.COLUMN_NAME == 'Edit'">
                                        <em class="fa fa-pencil text-success action-icon viewcomment"
                                            id="EditSchduleUpdate" v-on:click="Edit(data.row)"
                                            :title="data.column.COLUMN_NAME">
                                        </em>
                                    </template>

                                    <template v-if="data.column.COLUMN_NAME == 'CALIBRATION_DATE'">

                                        <span
                                            :title="$options.filters.formatDateTimeMMDDYY(data.row.CALIBRATION_DATE)">{{
                                                $options.filters.formatDateTimeMMDDYY(data.row.CALIBRATION_DATE)
                                            }}</span>
                                    </template>


                                </template>
                            </tg-list>

                        </div>

                    </div>

                </div>

            </div>

            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 text-right px-0 float-left">
                <em @click="previousTab" :disabled="activeTab === 1" class="btn btn-success text-white mr-2"
                    value="Previous" title="Previous" name="Previous"><i
                        class="fa fa-angle-double-left pr-2"></i><span>Previous</span></em>
                <em @click="nextTab" :disabled="activeTab === tabs.length" value="Next" title="Next" name="btnNext"
                    class="btn btn-primary text-white"><span>Next</span><i
                        class="fa fa-angle-double-right pl-2"></i></em>
            </div>
            <EditModel v-if="isEditModel" :HistoryItems="EditItems" :TabName="tabName"></EditModel>
        </div>
    </section>
</template>

<script>
import DataService from '../../service/DataService';
import { TreeSelectField } from 'tg-controls_cli/dist/myLib.common';
import EditModel from '../../components/Modal/AuditHistory.vue'

export default {
    components: {
        'tree-view': TreeSelectField,
        EditModel
    },
    data() {
        return {
            listheaderbuttons: [],
            LegendArray: [],
            CalibrationData: [],
            EditItems: [],
            Header: [{
                "COLUMN_NAME": "LOCATION_NAME",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("LocationName"),
                "DISPLAY_ORDER": 1,

            },
            {
                "COLUMN_NAME": "ITEM_CODE",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("ProductCode"),
                "DISPLAY_ORDER": 2,
            },
            {
                "COLUMN_NAME": "NAME",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("ProductName"),
                "DISPLAY_ORDER": 3,
            },
            {
                "COLUMN_NAME": "UNIQUE_NAME",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("UniqueName"),
                "DISPLAY_ORDER": 4
            },
            {
                "COLUMN_NAME": "FREQUENCY",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("Frequency"),
                "DISPLAY_ORDER": 5,
            },

            {
                "COLUMN_NAME": "CALIBRATION_DATE",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("DueDate"),
                "DISPLAY_ORDER": 6,
                settings: {
                    isInSlot: true
                }

            },
            {
                "COLUMN_NAME": "Edit",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("Edit"),
                "DISPLAY_ORDER": 7,
                settings: {
                    isInSlot: true
                }
            },

            ],
            widgets: [],
            noRecord: true,
            TotalRecord: 0,
            CurrentPage: 1,
            TotalPage: 0,
            itemName: '',
            tabName: 'Calibration',
            itemUniqueName: '',
            locationId: '',
            dueDate: '',
            frequency: '',
            SortBy: '',
            SortExp: '',
            PageNumber: 1,
            PageSize: 10,
            activeTab: 1,
            tabs: [
                { id: 1, title: 'Calibration' },
                { id: 2, title: 'Audit' },
                { id: 3, title: 'Image Capturing' },
            ],
            navheading: 'Maintenance Schedule',
            isLoading: false,
            isEditModel: false,
            DataLoaded: 0,
            Headerstext: 'Calibration',
            FormSchema: [
                {
                    layoutType: "four",
                    Data: [
                        {
                            astype: "TreeSelectField",
                            label: this.$t('Location'),
                            name: "Location",
                            value: null,
                            placeholder: "Select",
                            config: {
                                options: [],
                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Frequency'),
                            name: "Frequency",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": false },
                            config: {
                                options: [
                                    { name: "Annually", value: "annual" },
                                    { name: "Half yearly", value: "halfyear" },
                                    { name: "Quarterly", value: "quarterly" },
                                    { name: "Monthly", value: "month" },
                                    { name: "Bi-Weekly", value: "biweek" },
                                    { name: "Weekly", value: "week" },
                                    { name: "Daily", value: "daily" }

                                ],
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Product'),
                            name: "Product",
                            value: "",
                            placeholder: "Enter Product Name",
                            validationRules: { "required": false, max: 250 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('UniqueName'),
                            name: "UniqueName",
                            value: "",
                            placeholder: "Enter Unique Name",
                            validationRules: { "required": false, max: 250 },
                        },
                        {

                            astype: "DateField",
                            label: this.$t('DueDate'),
                            name: "DueDate",
                            value: '',
                            placeholder: "10/12/2022 12:AM",
                            config: { format: "M/D/YYYY" }
                        }

                    ]
                },
            ],
            button: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-search pr-2'></i>" + this.$t('Search'),
                    onClick: this.Search
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-refresh pr-2'></i>" + this.$t('ClearAll'),
                    onClick: this.Clear
                }
            ],
            PageNum: 1
        }
    },
    created: async function () {
        var vm = this;
        await vm.GetPageSize();
        await vm.FetchData();
        await vm.GetLocationByuserId();
        vm.DataLoaded = 1;
    },
    methods: {
        async GetPageSize() {
            var vm = this;
            var params = 'key=DefaultPageSize'
            await DataService.GetConfigurationValueForCompanyByKey(params).then((response) => {
                if (response.data != null) {
                    vm.PageSize = parseInt(response.data);
                }
            });
        },
        async GetLocationByuserId() {
            var vm = this;
            var params = `reqForm=`
            await DataService.GetLocationByCompanyId(params)
                .then(function (response) {
                    vm.FindalLocationArray = [];
                    vm.LocationDataTree = JSON.parse(response.data).data;
                    vm.LocationDataTree.forEach((value) => {
                        var CheckIfExist = vm.FindalLocationArray.filter(function (el) {
                            return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                        });
                        if (CheckIfExist.length == 0) {
                            if (value.PARENT_LOCATION_ID == null) {
                                let locationId = value.COMPANY_LOCATION_ID
                                let rslt = vm.getChildLocation(locationId, value, JSON.parse(response.data).data)
                                vm.FindalLocationArray.push(rslt)
                            }
                        }
                    });
                    vm.FormSchema[0].Data[0].config.options = vm.FindalLocationArray;
                });
        },
        getChildLocation: function (locationId, currentItem, data) {
            let ths = this;
            let treeArray = {};
            treeArray = {
                id: currentItem.COMPANY_LOCATION_ID,
                label: currentItem.LOCATION_NAME
            }
            let retrunArray = treeArray;
            var childArray = data.filter(function (el) {
                return el.PARENT_LOCATION_ID == locationId;
            });
            let finalChildArray = [];
            childArray.forEach((value) => {
                let ChildfolderId = value.COMPANY_LOCATION_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.PARENT_LOCATION_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getChildLocation(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        treeArray = {
                            id: `${value.COMPANY_LOCATION_ID}`,
                            label: `${value.LOCATION_NAME}`
                        }
                        finalChildArray.push(treeArray);
                    }
                }
            });
            retrunArray.children = finalChildArray;
            return retrunArray;
        },
        changeTab(tab) {
            var vm = this;
            if (tab == 1) {
                vm.SortBy = '';
                vm.SortExp = '';
                vm.Headerstext = 'Calibration';
                vm.tabName = 'Calibration';
                vm.FetchData();
            } else if (tab == 2) {
                vm.SortBy = '';
                vm.SortExp = '';
                vm.Headerstext = 'Audit';
                vm.tabName = 'Audit';
                vm.FetchData();
            }
            else {
                vm.SortBy = '';
                vm.SortExp = '';
                vm.Headerstext = 'Image Capturing';
                vm.tabName = 'ImageCapturing';
                vm.FetchData();
            }
        },
        previousTab() {
            if (this.activeTab > 1) {
                this.activeTab--;
                if (this.activeTab == 1) {
                    this.tabName = 'Calibration';
                    this.FetchData();
                }
                else if (this.activeTab == 2) {
                    this.tabName = 'Audit'
                    this.FetchData();
                }
                else {
                    this.tabName = 'ImageCapturing'
                    this.FetchData();
                }
            }
        },
        nextTab() {
            if (this.activeTab <= this.tabs.length - 1) {
                this.activeTab++;
                if (this.activeTab == 1) {
                    this.tabName = 'Calibration';
                    this.FetchData();
                }
                else if (this.activeTab == 2) {
                    this.tabName = 'Audit'
                    this.FetchData();
                }
                else {
                    this.tabName = 'ImageCapturing'
                    this.FetchData();
                }
            }
        },
        FetchData() {
            var vm = this;
            if (typeof (vm.locationId) == 'undefined' || vm.locationId == null) {
                vm.locationId = ''
            }
            vm.isLoading = true; var params = `tabName=${vm.tabName}&itemUniqueName=${vm.itemUniqueName}&locationid=${vm.locationId}&frequency=${vm.frequency}&dueDate=${vm.dueDate}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNum=${vm.PageNum}`;
            DataService.GetListScheduleUpdate(params).then(response => {
                if (response.data != null) {
                    if (JSON.parse(response.data).length > 0) {
                        vm.CalibrationData = JSON.parse(response.data);
                        vm.TotalRecord = vm.CalibrationData[0].TOTALRECORDS;
                        vm.TotalPage = Math.ceil(vm.TotalRecord / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                        vm.noRecord = false;
                        vm.isLoading = false;
                    }
                    else {
                        vm.noRecord = true;
                        vm.CalibrationData = [];
                        vm.TotalRecord = 0
                        vm.isLoading = false;
                    }
                }
            });
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        Search: function () {
            var vm = this;
            vm.locationId = vm.FormSchema[0].Data[0].value;
            vm.frequency = vm.FormSchema[0].Data[1].value;
            vm.itemName = vm.FormSchema[0].Data[2].value;
            vm.itemUniqueName = vm.FormSchema[0].Data[3].value;
            vm.dueDate = vm.FormSchema[0].Data[4].value;
            vm.FetchData();
        },
        Clear: function () {
            var vm = this;
            vm.FormSchema[0].Data[0].value = null;
            vm.FormSchema[0].Data[1].value = "";
            vm.FormSchema[0].Data[2].value = "";
            vm.FormSchema[0].Data[3].value = "";
            vm.FormSchema[0].Data[4].value = "";
            vm.locationId = '';
            vm.changeTab(this.activeTab);
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNum = value.PageNumber;
            this.FetchData();
        },
        Edit: function (items) {
            var vm = this;
            vm.EditItems = items;
            vm.isEditModel = true;
        },
        ClosePopup() {
            var vm = this;
            vm.isEditModel = false;
        },
        commonHeaderButtonClick: function () { },
        renderActions() { },
        renderRowActionMethod() { },
    }
}

</script>
<style>
tr.high-profile-bar td:first-child::after {
    background: #de0303;
}

.center-icon {
    display: flex;
    justify-content: center;
    align-items: center;
}

.vc-popover-content-wrapper {
    z-index: 9999 !important;
}
</style>
